.input {
    border-radius: 4px;
    color: rgba(0, 0, 0, 0.87);
    cursor: text;
    line-height: 1.4375rem;
    border: 1px solid #c4c4c4;
    width: 100%;
    height: 40px;
}

.input:focus-visible {
    outline: none;
}

.textarea {
    border-radius: 4px;
    color: rgba(0, 0, 0, 0.87);
    cursor: text;
    line-height: 1.4375rem;
    border: 1px solid #c4c4c4;
    width: 100%;
    height: 40px;
    resize: vertical;
}

.textarea:focus-visible {
    outline: none;
}

.box {
    margin-bottom: 20px;
}

.buttonBox {
    justify-content: right;
}
